import React, { useState } from 'react';
import CardBody from './CardBody';

// TODO: Add a comment explaining what export default does
// exports this file so others can call/use Counter()
export default function Counter() {
  // TODO: Add a comment that explains how the useState hook works
  // set a set variable in a way that allows us to update it later
  let [count, setCount] = useState(0);

  // TODO: Explain what is happening with this click handler
  // updating the count by + 1
  const handleIncrement = () => {
    setCount((count + 1));
  };

  // TODO: Explain what is happening with this click handler
  // updating the count by - 1
  const handleDecrement = () => {
    setCount((count - 1));
  };

  return (
    <div className="card text-center">
      <div className="card-header bg-primary text-white">Click Counter!</div>
      {/* TODO: Add a comment that explains what props are getting passed to CardBody */}
      {/* count, and the handlers are being passed down */}
      {/* THIS RIGHT HERE IS VERY IMPORTANT TO CONNECT COMPONENTS */}
      {/* parent (Counter) to child (CardBody) */}
      <CardBody
        count={count}
        handleIncrement={handleIncrement}
        handleDecrement={handleDecrement}
      />
    </div>
  );
}
